import Inferno from 'inferno';
import Component from 'inferno-component';
import defaultPlayerData from './defaultPlayerData';
import ActionButton from './../ActionButton/ActionButton.js';

export default class Player extends Component {
	constructor(props) {
		super(props);
		this.state = {
			id: this.props.id,
		};
		let defaultData = defaultPlayerData;
		defaultData.name = 'Player ' + Number(this.state.id + 1);
		this.state = Object.assign(this.state, defaultData);

		this.click = this.click.bind(this);
	}

	click() {
		this.setState({
			coins: this.state.coins + 1,
		})
	}

	render() {
		return (
			<div
				style={{
					display: 'flex',
					flexDirection: 'column',
					flex: 1,
					alignItems: 'center',
				}}
			>

				{/* player header */}
				<div style={{display: 'flex', padding: '10px 0px'}}>
					{this.state.name}
				</div>

				{/* player actions */}
				<div>
					<ActionButton onClick={this.click} content={this.state.coins} />
				</div>

			</div>
		);
	}
}



